@charset "UTF-8"; 
html,
body {
	height: 100%;
}

body {
	-webkit-text-size-adjust: none;
}

.container {
    display: grid;
    grid-template-columns: 1fr 5fr;
    grid-template-rows: 88px auto;
	min-height: 100vh;
}

//라벨
span[class*="label"] {
	display: inline-block;
	margin-left: 5px;
	padding: 2px 4px;
	min-width: 38px;
	height: 24px;
	font-size: 13px;
	font-weight: 600;
	line-height: 20px;
	text-align: center;
	@include box-sizing;
	@include radius (2.3px);
	vertical-align: middle;
	&:first-child{
		margin-left: 6px;
	}
	& + .label_gray_btn{
		margin-left: 5px;
	}
}
.label {
	&_green {
		color: #08bf5b;
		background-color: rgba( 8, 191, 91, 0.1);
	}
	&_blue {
		color: #2396e2;
		background-color: rgba( 35, 150, 226, 0.1);
	}

}


/* header */
.header {
    grid-column: 1 / span 3;
	border-bottom: 1px solid #efefef;
}

.header_inner {
	padding:  30px 24px 30px 23px;
	@include clear;
	.header_logo {
		float: left;
	}
    .link_logo {
        display: inline-block;
        vertical-align: top;
    
    }
    .link_txt {
        display: inline-block;
        font-size: 15px;
        font-weight: 600;
        color: #111;
        line-height: 27px;
        vertical-align: top;
    }	
}


/* gnb */
.gnb {
	float: right;
	font-size: 0;
	.gnb_item {
		display: inline-block;
		position: relative;
		vertical-align: middle;
		& + .gnb_item {
			margin-left: 30px;
		}
		&.gnb_mail{
			margin-top: -0.5px;
		}
	}
	.gnb_link {
		display: block;
        font-size: 12px;
        color: #111;
        line-height: 26px;
    }
    .langset_btn {
        display: block;
        border: none;
        background: #fff;
        padding: 5.5px 0 5px;
    }
    .arrow_down {
        display: inline-block;
        @include get-sprite(header_arrow_down, $sp_application);
        vertical-align: top;
        margin: 0 0 0 6px;
    }

	.gnb_btn {
        display: inline-block;
		padding: 5px 7px;
		@include radius (3px);
		border: solid 1px #dfdfdf;
		font-weight: bold;
		font-size: 12px;
  		line-height: 1;
		color: #616161;

	}

	.gnb_text {
		display: inline-block;
		margin-right: 8px;
		font-size: 12px;
		letter-spacing: 0.28px;
	}
}

/* snb */
.snb {
	padding: 30px 21px;
    border-right: 1px solid #efefef;
	overflow-y: auto;
}

.snb_group {
	& + .snb_group {
		margin-top: 39px;
	}

	&:nth-of-type(2n) {
		.nav_snb {
			margin-top: 6px;
		}
	}
}
.snb_title {
	padding-left: 2px;
	font-weight: normal;
	font-size: 13px;
	line-height: 30px;
	color: #949494;
	vertical-align: middle;
	@include tab{
		font-size: 12px;
	}
}

.nav_snb {
	margin-top: 9px;

	li + li {
        margin-top: 16px;
	}
	a {
		display: inline-block;
		position: relative;
		font-size: 15px;
		
		color: #111;
		&.active {
			font-weight: bold;
			.icon_menu_01 {
				@include get-sprite(icon-snb-productlist-black, $sp_application);
			}
			.icon_menu_02{
				@include get-sprite(icon-snb-registration-black, $sp_application);
			}
			.icon_menu_03{
				@include get-sprite(icon-snb-create-black, $sp_application);
			}
			.icon_menu_04{
				@include get-sprite(icon-snb-payment-black, $sp_application);
			}
		}
	}
	
	[class*=icon_menu] {
		display: inline-block;
		width: 24px;
		height: 24px;
		vertical-align: top;
		margin-right: 6px ;
	} 
	.icon_menu_01{
		@include get-sprite(icon-snb-productlist, $sp_application);
	}
	.icon_menu_02{
		@include get-sprite(icon-snb-registration, $sp_application);
	}
	.icon_menu_03{
		@include get-sprite(icon-snb-create, $sp_application);
	}
	.icon_menu_04{
		@include get-sprite(icon-snb-payment, $sp_application);
	}
	.snb_menu_txt {
		display: inline-block;
		vertical-align: top;
		padding-top: 2px;
		font-size: 16px;
		line-height: 20px;
		letter-spacing: -0.4px;
		@include tab{
			font-size: 14px;
		}
	}
}

